.game-register {
    padding: 20px;
    padding-top: 0px;
}

.game-register h1 {
    font-family: sans-serif;
    color: gray;
    font-weight: bold;
    margin-bottom: 40px;
    user-select: none;
    cursor: default;
}

.game-register .col {
    margin-right: 40px;
}

.game-register label {
    text-align: right;
    width: auto;
}

.game-register input {
    max-width: 300px;
    min-width: 300px;
    border-color: #69c521;
}

.game-register-top {
    text-align: right;
}

.game-register-top>div>span, .game-register-form .green {
    color: #69c521;
    font-family: sans-serif;
    font-size: 18px;
    font-weight: normal;
    text-align: center;
    user-select: none;
    cursor: default;
}

.game-register-form {
    margin-top: 20px;
}

.game-register-form input[type="radio"] {
    max-width: 20px;
    min-width: 20px;
}

.game-register-form .tribe label {
    margin-right: 10px;
    display: inline-block !important;
}

.server-card {
    margin-right: 0px !important;
    display: inline-block;
    /* max-width: 100px; */
    min-width: 200px;
    margin-left: 10px;
    font-size: 12px;
    cursor: pointer;
    user-select: none;
    -webkit-user-select: none;
    min-height: 85px;
    border-radius: 5px;
    border: 2px solid #0186a5;
    background-image: url(../images/banner.webp);
    /* background-position: -50px 58px; */
    background-attachment: fixed;
}

.server-card input {
    display: none;
}

.server-card input[type="checkbox"] {
    position: absolute;
    right: 3px;
    min-width: auto;
    cursor: pointer;
    top: 1vh;
}

.server-card input:checked+.card-holder {
    box-shadow: 0px 0px 2px 3px rgb(0, 117, 255);
}

.card-holder {
    border-radius: 5px;
}

.server-card input:checked+.card-holder input {
    display: inline;
}

.server-card-header {
    font-size: 16px;
    text-align: center;
    background-color: rgb(255 255 255 / 75%);
    font-family: system-ui;
    /* padding: 5px 0px; */
    position: relative;
    font-weight: bold;
    border-radius: 5px 5px 0px 0px;
}

.server-card-content {
    padding: 5px;
    background-color: rgb(255, 255, 255, 0.8);
    font-weight: bold;
    border-radius: 0px 0px 5px 5px;
}

@media only screen and (max-width: 680px) {
    .game-register h1 {
        margin-bottom: 25px;
    }
    .game-register .col {
        margin-right: 10px;
        margin-bottom: 10px;
        text-align: center;
    }
    .game-register label {
        display: none;
    }
    .game-register input {
        display: unset;
        max-width: 200px;
        min-width: auto;
    }
    .server-card input {
        display: none;
    }
}